import { Button, Modal, Form, Input } from 'antd';
import formItemLayout from './Style/style';
import { unwrapResult } from '@reduxjs/toolkit';
import { useState } from 'react';

export default function UpdatePassword({
  isInformation,
  setIsInformation,
  actionUpChangePassword,
  token,
}) {
  const [form] = Form.useForm();
  const [loading, setLoading] = useState(false);
  const onChangeInformationUser = async (value) => {
    if (value) {
      const data = {
        password: value.password.trim(),
      };
      setLoading(true);
      const resultPassword = await actionUpChangePassword(data, token);
      const currentPassword = unwrapResult(resultPassword);
      if (currentPassword) {
        setLoading(false);
        setIsInformation(false);
        form.resetFields(['password']);
        form.resetFields(['confirm']);
      }
    }
  };
  return (
    <Modal
      centered
      visible={isInformation}
      title="i mt khu"
      onCancel={() => setIsInformation(false)}
      footer={[
        <Form form={form} onFinish={onChangeInformationUser}>
          <Button key="back" onClick={() => setIsInformation(false)}>
            Hy
          </Button>
          <Button key="submit" htmlType="submit" type="primary" loading={loading}>
            Lu mt khu
          </Button>
        </Form>,
      ]}
    >
      <Form {...formItemLayout} form={form}>
        <Form.Item
          className="input-password"
          name="password"
          rules={[
            {
              min: 8,
              message: 'Mt khu qu ngn t nht 8 k t !',
            },
            {
              required: true,
              type: 'string',
              message: 'Vui lng nhp mt khu ca bn !',
            },
          ]}
          hasFeedback
        >
          <Input.Password placeholder="Mt khu mi" />
        </Form.Item>
        <Form.Item
          name="confirm"
          dependencies={['password']}
          hasFeedback
          rules={[
            {
              required: true,
              message: 'Vui lng xc nhp li mt khu !',
              type: 'string',
            },
            ({ getFieldValue }) => ({
              validator(rule, value) {
                if (!value || getFieldValue('password') === value) {
                  return Promise.resolve();
                }
                return Promise.reject('Mt khu bn  nhp khng khp !');
              },
            }),
          ]}
        >
          <Input.Password placeholder="Nhp li mt khu" />
        </Form.Item>
      </Form>
    </Modal>
  );
}
