import { useState } from 'react';
import { Link } from 'react-router-dom';
import { UserOutlined } from '@ant-design/icons';
// --ComPonent
import InForUser from './inforUser/index';
import Loading from 'component/LoadingBtn/index';
// --CSS
import './style.css';
export default function User({
  socket,
  user,
  setUser,
  idUser,
  setIdUser,
  token,
  setToken,
  loadingGetProfile,
}) {
  const [openUser, setOpenUser] = useState(false);
  const closeOpenUser = () => {
    setOpenUser(false);
  };
  {
    openUser
      ? document.querySelector('body').classList.add('active')
      : document.querySelector('body').classList.remove('active');
  }

  return (
    <>
      <div className="ground-user">
        {loadingGetProfile && <Loading />}
        {/* khng c token hin button ng nhp */}
        {!token && !loadingGetProfile && (
          <Link to="/login" className="items-login">
            ng nhp
          </Link>
        )}
        {!loadingGetProfile && (
          <div className="main-user">
            <div className="profile-login">
              {/* hin thng tin user nu c token */}
              {token && user ? (
                <InForUser
                  user={user}
                  token={token}
                  setUser={setUser}
                  socket={socket}
                  setToken={setToken}
                  idUser={idUser}
                  setIdUser={setIdUser}
                />
              ) : (
                <>
                  {/* khng c token */}
                  <UserOutlined
                    style={{ fontSize: '1.2em', color: '#ffff' }}
                    onClick={() => setOpenUser(true)}
                  />
                  <div className={`show-login ${openUser && 'open'}`}>
                    <Link to="/login" onClick={closeOpenUser}>
                      ng nhp
                    </Link>
                    <Link to="/register" onClick={closeOpenUser}>
                      ng k
                    </Link>
                  </div>
                  {openUser && <div className="active-before" onClick={closeOpenUser}></div>}
                </>
              )}
            </div>
          </div>
        )}
      </div>
    </>
  );
}
